


































































import shoppublic from '@/common/shoppublic'; //服务器地址
export default {
	data() {
		return {
			tabsState:'',//选项卡状态
			tabsMaskState:'',//选项卡遮罩显示隐藏
			swiperIndex:'',
			pagesize: 0,
			index:7,
			partnerList:[]
		};
	},
	onLoad() {
		this.getPartnerList();
	},
	onReachBottom() {
		this.getPartnerList('onReach');
	},
	methods:{
		// 获取老店二手列表
		getPartnerList(page = '0') {
			var _this = this;
			if(page === 'onReach'){
				uni.request({
					url:shoppublic.getUrl() + 'secondHandItem/findSecondHandItemList',
					data:{
						index:_this.index,
						city:610100,
						county:'',
						partner:'',
						type:'',
						statuslist:'状态',// 经营状态
						minarea: '',
						maxarea: '',
						minloadingfee: '',
						maxloadingfee: '',
						
					},
					success: (res) => {
						console.log(res);
						if(page === 'onReach' && res.data.responseBody){
							_this.index += 7;
						}else{
							return false;
						}
						var value = res.data.responseBody
						_this.moneyIncise(value);
					}
				})
			}else{
				uni.request({
					url:shoppublic.getUrl() + 'secondHandItem/findSecondHandItemList',
					data:{
						index:_this.pagesize,
						city:610100,
						county:'',
						partner:'',
						type:'',
						statuslist:'状态',// 经营状态
						minarea: '',
						maxarea: '',
						minloadingfee: '',
						maxloadingfee: '',
					},
					success: (res) => {
						console.log(res);
						var value = res.data.responseBody
						_this.moneyIncise(value);
					}
				})
			}
		},
		// 钱切割
		moneyIncise(value){
			console.log(value);
			if(value){
				for (let i in value) {
					if (value[i].price >= 1000) {
						var money = value[i].price
						if (money.toString().indexOf('.') == -1) {
							value[i].price = value[i].price
						}else{
							value[i].price = (value[i].price).toFixed(1)
						}
					} else {
						value[i].price = '面议'
					}
				}
			}
			if(this.partnerList.length > 1){
				this.partnerList = this.partnerList.concat(value);
			}else{
				this.partnerList = value;
			}
		},
		// 区域/费用选择器按钮点击事件
		clickScrollCell(e){
			this.closeTabsMask();
		},
		// 顶部选项卡点击事件
		tabsChanges(state){
			this.tabsState = state;
			this.swiperIndex = state;
			this.tabsMaskState = 'true';
		},
		// 选项卡滑块切换
		swiperChangs(e){
			this.tabsState = e.detail.current;
			this.swiperIndex = e.detail.current;
		},
		// 关闭遮罩
		closeTabsMask(){
			this.tabsState = '';
			this.tabsMaskState = '';
			this.swiperIndex = '';
		},
		// 跳转至二手转让发布页
		toOldStoreTwoHandRelease(){
			uni.navigateTo({
				url:"./oldStoreTwoHandRelease/askToBuy/askToBuy"
			})
		},
		// 跳转至二手物品详情
		toOldStoreTwoHandDateils(id){
			uni.navigateTo({
				url:"./oldStoreTwoHandDateils/oldStoreTwoHandDateils?id=" + id
			})
		},
	}
}
